import * as React from 'react';
import type { SinglePilet } from 'piral-base';
import Page from './Page'
import { Link } from 'react-router-dom';

const DemoDiv = ({ children, piral, dynamicValue }) => {

	const [storeData, setStoreData] = React.useState(null)
	React.useEffect(()=>{
		fetch('https://fakestoreapi.com/products')
            .then(res=>res.json())
            .then(json=>console.log(json))
	},[])
	
    return <div className='fetch-pilet-div' style={{padding:"10px",background: "lightblue"}}>
		<p>LocalPilet</p>
		<p>This is fetched from piral.getdata --- foo: {piral.getData("mobileNo")}</p>
		<p>This is fetched from piral.getdata and then put to state --- dynamicValue: {dynamicValue}</p>
        {children}
    </div>
}

// allow-unused-export -- pulled in via require()
export const checkOutPilet: SinglePilet = {
	name: 'checkoutPilet',
	version: '0.0.1',
	spec: 'v2',
	dependencies: {},
	config: {},
	basePath: '',
	link: 'fakelink',
	setup(api) {

		function usePiralStoreValueHook(name){
			const [value, setValue] = React.useState("")
			React.useEffect(()=>{
				const handler = (ev) => {
					console.log("store-data listen",ev.name)
					if (ev.name === name) {
						setValue(ev.value)
					}
				};
				api.on("store-data", handler);

			},[name])
			
		
			return value
		}

		
		api.registerPage("/checkout", Page);
		api.registerMenu(()=> <Link to="/checkout">Checkout</Link>);


		api.registerExtension('fetch-extention', ({piral}) => {
			const dynamicValue = usePiralStoreValueHook("dynamicValue");
			return <DemoDiv piral={piral} dynamicValue={dynamicValue}>This came through a pilet extension slot</DemoDiv>
		});
	},
};